headers1 = headers1.append('Content-Type', 'application/json');
headers1 = headers1.append('Authorization', 'application/json');
@Injectable({ providedIn: "root" })
export class SearchService {
    baseUrl = `${environment.serviceBaseUrl}`;
    constructor(private httpClient: HttpClient) { }
    searchQuote(searchQuoteParams) {
        return this.httpClient.post("http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7777/searchQuote", 
        searchQuoteParams,{headers: headers1});
    }
    searchProposal(searchQuoteParams) {
        return this.httpClient.post("http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7777/searchProposal", 
        searchQuoteParams,{headers: headers1});
    }
    searchPolicy(searchQuoteParams) {
        return this.httpClient.post("http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7777/searchPolicy", 
        searchQuoteParams,{headers: headers1});
    }
}