import { Component, OnInit } from '@angular/core';
import { searchQPPModel } from 'src/app/core/models/SearchQPP';
import { CommonService } from 'src/app/core/services/common-service';
import { DatePipe } from '@angular/common';
import { SearchService } from 'src/app/core/services/search-service';

@Component({
  selector: 'app-searchpolicy',
  templateUrl: './searchpolicy.component.html',
  styleUrls: ['./searchpolicy.component.css']
})
export class SearchpolicyComponent implements OnInit {
  riskLocationIncludesBasement = true;

  veiwPolicy = {
    viewBy: 'Last 7days'
  };

  searchPolicyParams = new searchQPPModel('POLICY');
  PolicySearchResults: any = null;
  lobList: any;
  lobProductList: any;
  lobCode: any;
  isFormSubmitInValid: boolean;
  constructor(private commonService: CommonService, private datePipe: DatePipe, private searchService: SearchService) { }

  searchParams1 = 
  {
    user_id: "2003",
    firstname: null,
    policy_no: null
  }

  ngOnInit() {
    this.getLOBs();
  }

  resetCustomSelection() {
    this.veiwPolicy.viewBy = "Last 7days";
  }

  getLOBs() {
    this.commonService.getLOBs().subscribe((res) => {
      this.lobList = res
    });
  }

  getProductsByLobCode(lobCode: string) {
    console.log(lobCode);
    this.commonService
      .getProductsByLOBCode(lobCode)
      .subscribe((res) => (this.lobProductList = res));
  }

  onLobChange(event, lob) {
    this.lobCode = event.target.value;
    console.log(this.lobCode);
    this.getProductsByLobCode(this.lobCode);
  }

  onViewByChange(event) {
    this.searchPolicyParams.viewBy = event.target.value;
  }

  validations() {
    if ((this.searchPolicyParams.lob == "") && (this.searchPolicyParams.product == "") && (this.searchPolicyParams.customerName == "") && (this.searchPolicyParams.documentNo == "") && (this.searchPolicyParams.emailId == null) && (this.searchPolicyParams.mobileNo == null)) {
      this.isFormSubmitInValid = true;
    }
    else {
      this.isFormSubmitInValid = false;
      console.log(this.searchPolicyParams);
      // console.log(this.searchQuoteParams.pol_start_frm);
      if (this.veiwPolicy.viewBy == "Custom") {
        this.searchPolicyParams.pol_start_frm = this.datePipe.transform(this.searchPolicyParams.pol_start_frm, 'dd-MMM-yy');
        this.searchPolicyParams.pol_start_to = this.datePipe.transform(this.searchPolicyParams.pol_start_to, 'dd-MMM-yy');

      }
      this.searchService.searchPolicy(this.searchParams1).subscribe(res => {
        this.PolicySearchResults = res;
        console.log(res, this.PolicySearchResults)
      });
    }
  }
  clear()
  {
    this.searchParams1.firstname = null;
    this.searchParams1.policy_no = null;
    this.PolicySearchResults = null;
  }
}
