
import { Component, OnInit } from "@angular/core";
import { HttpErrorResponse } from '@angular/common/http';
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';

@Component({
  selector: 'app-searchcustomer',
  templateUrl: './searchcustomer.component.html',
  styleUrls: ['./searchcustomer.component.css']
})
export class SearchcustomerComponent implements OnInit {
  token: any;
  constructor(private createQuoteService: CreateQuoteService, private commonService: CommonService) {
    this.gettoken();
  }

  lineOfBusiness = '';;
  products = '';
  searchParams = {
    strLVTokenID: this.token,
    strLVCustomerName: "",
    strLVCustomerId: "",
    strLVCustomerType: "I",
    strLVState: "",
    strLVCityDistrict: "",
    strLVAreaVillage: "",
    strLVPincode: "",
    strLVPincodeLocality: "",
    strLVIntrEmpTag: "",
    strLVUserId: "",
  };

  searchParams1 = 
  {
    user_id: "2003",
    firstname: null,
    lastname: null,
    mobile_no: null,
    email_id: null,
    customer_id: null,
    policy_no: null
  }
  selectedType = "Select";

  customerDetails: any;
  isValidated = false;
  lobList: any;
  lobProductList: any;
  lobCode: string;
  getLOBs() {
    this.commonService.getLOBs().subscribe((res) => {
      this.lobList = res
    });
  }

  onLobChange(event, lob) {
    this.lobCode = event.target.value;
    console.log(lob.control);
    this.getProductsByLobCode(this.lobCode);
  }

  getProductsByLobCode(lobCode: string) {
    this.commonService
      .getProductsByLOBCode(lobCode)
      .subscribe((res) => (this.lobProductList = res));
  }

  ngOnInit() {
    this.getLOBs();
  }

  clear() {
    this.isValidated = false;
    this.customerDetails = [];
    // this.searchParams.strLVCustomerName = "";
    // this.searchParams.strLVCustomerId = "";
    // this.searchParams.strLVCustomerType = "I";
    // this.searchParams.strLVState = "";
    // this.searchParams.strLVCityDistrict = "";
    // this.searchParams.strLVAreaVillage = "";
    // this.searchParams.strLVPincode = "";
    // this.searchParams.strLVPincodeLocality = "";
    // this.searchParams.strLVIntrEmpTag = "";
    // this.searchParams.strLVUserId = "";

    this.searchParams1.firstname = null;
    this.searchParams1.lastname = null,
    this.searchParams1.mobile_no = null,
    this.searchParams1.email_id = null,
    this.searchParams1.customer_id = null,
    this.searchParams1.policy_no = null
    this.customerDetails = null;
    
  }

  validations() {
    if (
      this.searchParams1.firstname == null &&
      this.searchParams1.customer_id == null
    ) {
      this.isValidated = true;
    } else {
      this.isValidated = false;
      this.getCustomerDetails();
    }
  }
  getCustomerDetails() {
    //this.searchParams.strLVTokenID = this.token;
    if (!this.isValidated) {
      this.createQuoteService
        .searchCustomerDetails(this.searchParams1)
        .subscribe(
          (res) => {
            this.customerDetails = res;
            //this.customerDetails = this.customerDetails.lovtypes.lovtype;
          },
          (error) => () => {
          }
        );
    }
  }

  gettoken() {
    this.commonService.getToken().subscribe(
      (res) => {
        this.token = res;
      },
      (error: HttpErrorResponse) => {
        this.token = error.error.text; // body
      }
    );
  }
}
