import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router'
import { SearchComponent } from './search-component';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { SearchcustomerComponent } from './searchcustomer/searchcustomer.component';
import { SearchpolicyComponent } from './searchpolicy/searchpolicy.component';
import { SearchproposalComponent } from './searchproposal/searchproposal.component';
import { SearchquickquoteComponent } from './searchquickquote/searchquickquote.component';
import { SharedModule } from 'src/app/shared/shared.module';
const routes: Routes = [
    {
        path: '',
        component: SearchComponent
    }
];

@NgModule({
    imports: [CommonModule, FormsModule, SharedModule, RouterModule.forChild(routes)],
    exports: [RouterModule],
    declarations: [SearchComponent,
        SearchcustomerComponent,
        SearchpolicyComponent,
        SearchproposalComponent,
        SearchquickquoteComponent,]
})

export class SearchModule { }